.App-logo {
  height: 40px;
  pointer-events: none;
  margin-top: 20px;
  padding: 0;
  float:right;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 200s linear;
  }
}

/* ScrollBar */

/* width */

::-webkit-scrollbar {
  width: 20px;
}

/* Track */

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0);
  border-radius: 10px;
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: #f8f8f8;
  border-radius: 10px;
}

/* Header */

.header {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  text-align: center;
}

.header small {
  color: green;
}


/* Subheader */
.subHeader{
  text-align: center;
}

.subHeader h3{
    margin-bottom: 0;
    font-size: 40px;
}

.subHeader small{
    color: green;
    font-size: 9px;
    padding-left: 10px;
    display: block;
    margin-top: -2px;
}


/* Footer */

.footer {
  text-align: center;
}

.last-update-container {
  align-self: center
}

.footer-content {
  text-align: center;
  margin: 20px 0 30px 0;
}

